/* Package section */
.packages {
  background-color: white;
  padding: 3rem 1rem;
  margin: 4rem 0rem;
  margin-bottom: 0;
  padding-bottom: 0;
}

.package-types ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  margin: 0 auto;
  padding: 0;
}

.package-types ul li {
  list-style: none;
  width: auto;
  padding: 0.2rem;
  text-align: center;
}

/* animation for horizontal line below the package type */
.package-types ul li::after {
  content: "";
  display: block;
  width: 0;
  height: 2px;
  background: var(--secondary);
  transition: width 0.3s;
}

.package-types ul li:hover::after {
  width: 100%;
  transition: width 0.3s;
}

.package-types ul li a {
  text-decoration: none;
  color: black;
  font-size: 0.9rem;
  font-weight: 500;
}

.package-types ul li a:hover {
  font-weight: 600;
  transition: all ease-out 0.4s;
}

.package-cards {
  display: flex;
  /* flex-wrap: wrap; */
  justify-content: space-between;
  width: 85%;
  max-width: 80rem;
  margin: 2rem auto;
  padding: 0;
}

.package-card {
  position: relative;
  width: 20rem;
  min-width: 200px;
  height: max-content;
  display: flex;
  flex-direction: column;
  background: #ffffff;
  border-radius: 5px;
  margin: 0px 10px;
  padding: 5px 5px;
}

.package-card__heading {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

