/* Media Queries */
@media screen and (max-width: 960px) {

  /* For Responsive Navbar */
  .mobile-navbar-btn {
    display: block;
    z-index: 2;
    border: 3px solid #101010;
  }

  .container {
    position: relative;
  }

  .menu {
    width: 100%;
    height: 100vh;
    background-color: var(--primary);
    position: absolute;
    top: 88px;
    left: 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    /* to get the transition */
    transform: translateX(100%);
    transition: transform 0.5s ease-in-out;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
  }

  .menu-list {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 0;
    gap: 2rem;
  }

  /* For Active class of menu */

  .activeNav .menu {
    transform: translateX(0);
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
  }

  .activeNav .mobile-nav-icon:nth-child(1) {
    display: none;
  }

  .activeNav .mobile-nav-icon:nth-child(2) {
    display: block;
  }

  .input {
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    width: 100%;
  }

  .ourtalent {
    flex-direction: column;
    align-items: center;
    margin-bottom: 8rem !important;
  }

  .hero-content input {
    width: 70%;
    text-align: center;
    margin: 0;
  }

  .hero-content button {
    width: 70%;
  }

  .ourtalent-text {
    padding: 2rem 0rem;
    margin: 0;
    width: 100%;
    text-align: center;
  }

  .ourtalent-text__button {
    margin: auto;
    margin-top: 1rem !important;
  }

  .ourtalent-image {
    position: relative;
    width: 100%;
  }

  .ourtalent-image img:nth-child(2) {
    left: 11rem;
  }

  .ourtalent-image img:nth-child(1) {
    margin: 2rem 0 2rem 10rem;
  }
}

@media screen and (max-width: 860px) {
  .ourtalent-image img:nth-child(2) {
    left: 9rem;
  }

  .ourtalent-image img:nth-child(1) {
    margin: 2rem 0 2rem 8rem;
  }

  .process-container__card {
    width: 45%;
  }
}

@media screen and (max-width: 768px) {
  .package-types ul {
    flex-wrap: wrap;
    width: 60%;
    min-width: 30rem;
  }

  .package-cards {
    flex-direction: column;
    align-items: center;
    gap: 2rem;
  }

  .whatwedo__cards {
