    align-items: center;
  }

  .process-container {
    flex-direction: column;
    align-items: center;
  }

  .faq-container {
    flex-direction: column;
    align-items: center;
  }

  .connectus {
    flex-direction: column;
    align-items: center;
  }

  .contactus {
    flex-direction: column;
    align-items: center;
    gap: 4rem;
  }

  .contactus-container__right {
    width: 80% !important;
